___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --board-width: 500px;
  --board-cell-width: 8;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  line-height: 1.5rem;
}

body {
  /* background-color: grey; */
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  height: 100vh;
}

body > div {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

body > div > div {
  width: 1200px;
  height: 840px;
  padding: 40px;

  border-radius: 20px;
  background-color: rgb(0 0 0 / 0.5);
  text-align: center;

  color: white;
}

h1 {
  font-size: 4rem;
}

.winnerBanner {
  font-size: 3rem;
  margin-top: 40px;
}

.gameboard {
  border: solid 1px black;
  width: var(--board-width);
  height: 500px;

  display: flex;
  flex-direction: column;
}

.gameboardHeaders {
  margin-top: 50px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  font-size: 1.4rem;
}

.boardsDiv {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.gameboardRow {
  flex-grow: 1;
  display: flex;
}

.gameboardCell {
  border: solid 1px black;
  width: calc(var(--board-width) / var(--board-cell-width));
  color: white;
  cursor: pointer;
  user-select: none;
  font-size: 1.4rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.gameboardCellComputer:hover {
  border: solid 1px red;
}

.noInteraction {
  background-color: blue;
}

.playerBattleship {
  background-color: green;
}

.hit {
  background-color: red;
}

.sunk {
  background-color: black;
}

button {
  border: none;
  margin-top: 20px;
  width: 100px;
  height: 40px;
  border-radius: 20px;
  font-size: 1.2rem;
}

button:hover {
  background-color: lightgray;
}
